.section {
  position: relative;
  min-width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header {
  position: absolute;
  top: 20%;
  color: white;
  font: 96px 'Nautilus Pompilius';
}

.subheader {
  position: absolute;
  top: 40%;
  width: 80%;
  color: white;
  font: 36px 'Nautilus Pompilius';
  text-align: center;
}

.donateButton {
  position: absolute;
  top: 40%;
}

@media only screen and (max-width: 600px) {
  .header {
    font-size: 56px;
    top: 25%;
  }

  .subheader {
    font-size: 28px;
  }

  .donateButton {
    scale: .8;
  }
}

@media only screen and (max-width: 400px) {
  .header {
    font-size: 42px;
    top: 25%;
  }

  .subheader {
    top: 35%;
  }
}