.section {
  position: relative;
  min-width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header {
  position: absolute;
  font-size: 96px;
  top: 20%;
  font-family: 'Nautilus Pompilius';
  color: white;
}

.subheader {
  position: absolute;
  font-size: 36px;
  width: 80%;
  top: 40%;
  font-family: 'Nautilus Pompilius';
  color: white;
  text-align: center;
}

@media only screen and (max-width: 800px) {
  .header {
    font-size: 64px;
  }

  .subheader {
    font-size: 32px;
  }
}

@media only screen and (max-width: 600px) {
  .header {
    font-size: 56px;
    top: 20%;
  }

  .subheader {
    font-size: 32px;
    top: 35%;
  }
}

@media only screen and (max-width: 410px) {
  .header {
    font-size: 48px;
    top: 20%;
  }

  .subheader {
    font-size: 24px;
    top: 35%;
  }
}