.redirects {
  position: absolute;
  right: 1%;
  top: 2%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}

.redirect {
  position: relative;
  height: 50px;
  width: 50px;
  border-radius: 100%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.redirect img {
  pointer-events: none;
}

@media only screen and (max-width: 600px) {
  .redirects {
    right: 3%;
  }
  
  .redirect {
    width: 40px;
    height: 40px;
  }
}