.footer {
  position: absolute;
  width: 100%;
  height: 10%;
  bottom: 0%;
  background: #0e51af;
  animation: fade 1s linear forwards;
  overflow: visible;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wave {
  position: absolute;
  width: 200%;
  height: 20px;
  top: 0%;
  background-image: url('../assets/wave.svg');
  background-repeat: repeat-x;
  background-size: 10%;
  animation-name: wave;
  animation-duration: 30s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.footer-text {
  position: absolute;
  top: 50%;
  color: #1060d3;
  font-family: 'Nautilus Pompilius';
  font-weight: lighter;
  font-size: 20px;
}

@keyframes wave {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: -200% 0;
  }
}

@media only screen and (max-width: 800px) {
  .wave {
    height: 35px;
    background-size: 30%;
  }
}