.navigator {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav {
  position: fixed;
  width: 60px;
  height: auto;
  transition: filter .5s;
  cursor: pointer;
  opacity: .75;
}

.nav:hover {
  filter: brightness(2);
}

.nav.right { right: 2%; }
.nav.left { left: 2% }

.nav-content {
  position: absolute;
  width: fit-content;
  min-width: 100%;
  height: 100%;
  right: 0%;
  transition: right 1s;
  display: flex;
  flex-direction: row;
  flex-basis: 100%;
  overflow: visible;
}

.nav-content > * {
  transition: opacity .5s, transform .5s;
}

*[nav-state=unshown] {
  opacity: 0.1;
  transform: scale(.5);
}

.nav-bullets {
  position: absolute;
  top: 3%;
  height: 20px;
  width: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.bullet {
  position: relative;
  width: 15px;
  height: 15px;
  background-color: #0e51af;
  border-radius: 100%;
  cursor: pointer;
  transition: background-color .25s, scale .25s;
}

.bullet[nav-state=active] {
  background-color: white;
  scale: 1.25;
}

@media only screen and (max-width: 600px) {
  .nav { bottom: 10.5%; scale: .8; }

  .nav-bullets {
    top: unset;
    bottom: 13%;
  }
}