@import url('http://fonts.cdnfonts.com/css/nautilus-pompilius');
@import url('./Footer.css');
@import url('./Navigator.css');
@import url('./Redirects.css');

* {
  margin: 0%;
  overflow: hidden;
  user-select: none;
}

.wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  scrollbar-width: none;
  background-color: #1060d3;
  background-size: 90% 90%;
  animation: fade 1s linear forwards;
}

.wrapper::-webkit-scrollbar {
  display: none;
}

.content {
  position: absolute;
  width: 100%;
  height: 100%;
  animation: fade .5s 1.5s linear forwards;
  opacity: .001;
  display: flex;
  justify-content: center;
}

img[alt=logo-static] {
  position: absolute;
  width: 100px;
  height: auto;
  top: -2%;
  left: 0%;
}

a {
  color: #71fff8;
}

@keyframes fade {
  0% {
    opacity: .001;
  }
  
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 300px) {
  .redirects {
    right: unset;
  }

  img[alt=logo-static] {
    display: none;
  }

  .nav {
    display: none;
  }
}